@charset "utf-8";

@use "../global";

body {
  color: global.$color_darkgray;
  letter-spacing: 0.025em;
  @include global.line_height(32, 14);
  @include global.fz(14);
  @include global.font-JP_sans;
  @include global.md {
    @include global.line_height(36, 16);
    @include global.fz(16);
  }
}

* {
  box-sizing: border-box;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  &::before,
  &::after {
    box-sizing: inherit;
  }
  &:focus {
    outline: none;
  }
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  padding: 0;
  margin: 0;
  border: 0;
}

a {
  outline: none;
  -webkit-touch-callout: none;
  &,
  &:visited {
    color: inherit;
  }
  &:hover {
    @include global.md {
      text-decoration: none;
    }
  }
}

article,
aside,
footer,
header,
nav,
section,
main {
  display: block;
}

ol,
ul {
  list-style: none;
}

img,
video {
  max-width: 100%;
}

img {
  height: auto;
  vertical-align: top;
  border-style: none;
  @include global.under_lg {
    width: 100%;
  }
}

blockquote,
q {
  quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
  content: none;
}

b,
em,
strong,
h1,
h2,
h3,
h4,
h5,
h6 {
  @include global.font-JP_bold_sans;
}

address {
  font-style: normal;
}

small {
  font-size: 87.5%;
}

[hidden] {
  display: none !important;
}

[disabled] {
  cursor: not-allowed;
}

/* form */
input[type="text"],
input[type="tel"],
input[type="number"],
input[type="date"],
input[type="time"],
input[type="password"],
input[type="email"],
textarea {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

textarea {
  display: block;
}

fieldset {
  margin: 0;
  border: none;
}

legend {
  display: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}
