@charset "utf-8";

@use "../global";

.p-entry {
  .p-entryHeading {
    position: relative;
    display: grid;
    padding-bottom: global.vw_sp(20);
    margin-bottom: global.vw_sp(40);
    border-bottom: 2px solid global.$color_border;
    @include global.md {
      padding-bottom: 20px;
      margin-bottom: 40px;
    }
    &::after {
      position: absolute;
      left: 0;
      bottom: -2px;
      display: block;
      width: global.vw_sp(80);
      height: 2px;
      content: "";
      background: global.$color_primary;
      @include global.md {
        width: 80px;
      }
    }
    .p-entryHeading__title {
      order: 2;
      letter-spacing: 0.075em;
      @include global.font_weight_bold;
      @include global.line_height(34, 20);
      @include global.fz(20);
      @include global.md {
        @include global.line_height(34, 22);
        @include global.fz(22);
      }
    }
    .p-entryHeading__meta {
      order: 1;
      margin-bottom: global.vw_sp(5);
      @include global.md {
        margin-bottom: 5px;
      }
      .p-entryHeading__time {
        color: #b7b7b7;
        letter-spacing: 0.02em;
        white-space: nowrap;
        @include global.line_height(20, 14);
        @include global.font-EN(500);
        @include global.md {
          @include global.line_height(20, 16);
        }
      }
    }
  }
  padding-bottom: global.vw_sp(100);
  @include global.lg {
    padding-bottom: 110px;
  }
  > :first-child {
    margin-top: 0 !important;
  }
  > :last-child {
    margin-bottom: 0 !important;
  }
  // 見出し
  > h1 {
    position: relative;
    margin-bottom: 30px;
    border-bottom: 1px solid #cecece;
    @include global.fz(24);
    @include global.md {
      padding-bottom: 16px;
      margin-bottom: 40px;
    }
    &::after {
      position: absolute;
      bottom: -2px;
      left: 0;
      display: block;
      width: 113px;
      height: 4px;
      content: "";
      background: global.$color_primary;
      @include global.md {
        width: 113px;
        height: 4px;
      }
    }
  }
  > h2 {
    position: relative;
    padding: 18px 20px;
    margin-top: 110px;
    margin-bottom: 44px;
    letter-spacing: 0.025em;
    background-color: #ebf9ff;
    @include global.font_weight_bold;
    @include global.line_height(32, 20);
    @include global.fz(20);
  }
  > h3 {
    padding-bottom: 19px;
    margin-top: 44px;
    margin-bottom: 44px;
    letter-spacing: 0.025em;
    border-bottom: 2px solid global.$color_primary;
    @include global.line_height(36, 18);
    @include global.fz(18);
  }
  > h4 {
    display: flex;
    margin-top: 25px;
    margin-bottom: 12px;
    color: global.$color_primary;
    letter-spacing: 0.025em;
    @include global.line_height(28, 18);
    @include global.fz(18);
    &::before {
      flex-shrink: 0;
      width: 12px;
      height: 2px;
      margin-top: 0.7em;
      margin-right: 10px;
      content: "";
      background-color: global.$color_primary;
    }
  }
  > h5 {
    margin-top: 20px;
    margin-bottom: 16px;
    letter-spacing: 0.025em;
    @include global.line_height(28, 18);
    @include global.fz(18);
  }
  > h6 {
    margin-top: 20px;
    margin-bottom: 16px;
    letter-spacing: 0.025em;
    @include global.fz(16);
  }
  h2,
  h3,
  h4,
  h5,
  h6 {
    & + ul:not([class]),
    & + ol:not([class]) {
      margin-top: 0;
    }
  }

  // 本文テキスト
  > p {
    margin: global.vw_sp(40) 0;
    letter-spacing: 0.025em;
    word-wrap: break-word;
    @include global.line_height(28, 16);
    @include global.fz(16);
    @include global.lg {
      margin: 14px 0;
    }
    & + p {
      margin-top: 1em;
    }
  }

  // リンク
  a {
    color: global.$color_primary;
    text-decoration: underline;
    @include global.hover {
      text-decoration: none;
    }
  }

  // リスト
  ul:not([class]) {
    margin-top: 2.7em;
    margin-bottom: 2.7em;
    margin-left: 0.3em;
    > li {
      word-break: break-all;
      text-indent: -15px;
      padding-left: 15px;
      @include global.line_height(28, 14);
      @include global.md {
        @include global.line_height(28, 16);
      }
      & + li {
        margin-top: 16px;
      }
      &::before {
        display: inline-flex;
        flex-shrink: 0;
        width: 7px;
        height: 7px;
        margin-top: 0.4em;
        margin-right: 8px;
        content: "";
        background-color: #89A9C4;
        border-radius: 4px;
      }
      > ul {
        margin-left: 0.5em;
      }
      > ol {
        margin-left: 1.5em;
      }
    }
  }
  ol:not([class]) {
    margin-top: 2.7em;
    margin-bottom: 2.7em;
    counter-reset: ol-list;
    @at-root .p-entry .p-entry__inner > h4 + ol:not([class]) {
      margin-top: 0;
    }
    @at-root .p-entry .p-entry__body > h4 + ol:not([class]) {
      margin-top: 0;
    }
    ::marker {
      @include global.fz(14);
    }
    > li {
      position: relative;
      padding-left: 20px;
      text-indent: 0;
      @include global.line_height(28, 14);
      @include global.md {
        @include global.line_height(28, 16);
      }
      & + li {
        margin-top: 14px;
      }
      &::before {
        position: absolute;
        top: 0.2em;
        left: 0;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 16px;
        height: 16px;
        line-height: 1;
        content: counter(ol-list) ".";
        counter-increment: ol-list;
        @include global.font-EN(600);
        @include global.fz(18);
      }
    }
  }
  > .c-list {
    margin-left: 1em;
    list-style-type: decimal;
    > li {
      position: relative;
      letter-spacing: 0.025em;
      @include global.line_height(20, 15);
      @include global.fz(15);
      + li {
        margin-top: 13px;
      }
    }
  }

  // 画像
  > p > img {
    @include global.under_md {
      width: 100%;
      height: auto;
      margin-bottom: 30px;
    }
    @include global.md {
      width: auto;
    }
  }
  > .wp-block-image {
    margin-top: 30px;
    margin-bottom: 30px;
    figcaption {
      margin-top: global.vw_sp(10);
      line-height: 1.1;
      text-align: left;
      letter-spacing: 0.05em;
      @include global.fz(11);
      @include global.md {
        margin-top: 10px;
        @include global.line_height(22, 12);
        @include global.fz(12);
      }
    }
    .alignleft {
      margin-right: 38px;
    }
    .alignright {
      margin-right: 38px;
    }
  }

  // テーブル
  .wp-block-table {
    margin-top: global.vw_sp(40);
    margin-bottom: 20px;
    border-color: #C3C3C3 !important;
    @include global.md {
      margin-top: 40px;
    }
    thead {
      border: none !important;
      th {
        background: #C0D6E8;
      }
    }
    th {
      padding: 16px 20px;
      text-align: left;
      letter-spacing: 0.025em;
      background-color: #F2F8FD;
      border-color: #C3C3C3 !important;
      @include global.font_weight_bold;
      @include global.line_height(28, 16);
      @include global.fz(16);
    }
    td {
      padding: 16px 20px;
      letter-spacing: 0.025em;
      border-color: #C3C3C3 !important;
      @include global.line_height(24, 16);
      @include global.fz(16);
    }
    //@include global.under_md {
    //  table {
    //    border-bottom: 1px solid #C3C3C3 !important;
    //    border-left: 1px solid #C3C3C3 !important;
    //    border-right: 1px solid #C3C3C3 !important;
    //  }
    //  th,
    //  td {
    //    border: none !important;
    //  }
    //  th,
    //  td {
    //    border-top: 1px solid #C3C3C3 !important;
    //  }
    //}
    //&:not(.c-tableCol) {
    //  @include global.under_md {
    //    table,
    //    thead,
    //    tbody,
    //    tr,
    //    th,
    //    td {
    //      display: block;
    //      width: 100%;
    //    }
    //  }
    //}
    //&.c-tableCol {
    //  @include global.under_md {
    //    thead {
    //      display: none;
    //    }
    //    table,
    //    tr,
    //    th,
    //    td {
    //      display: block;
    //      width: 100%;
    //    }
    //  }
    //}
  }

  // ボタン
  > .wp-block-buttons {
    display: flex;
    flex-wrap: wrap;
    margin-top: 30px;
    margin-bottom: 30px;
    .wp-block-button {
      display: inline-block;
      .wp-block-button__link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 10px 20px;
        margin-right: 10px;
        @include global.fz(18);
      }
    }
  }

  // ファイルダウンロード
  > .wp-block-file {
    display: flex;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
    a:first-of-type {
      @include global.fz(16);
    }
    .wp-block-file__button {
      margin-left: 10px;
    }
  }

  // 動画（videoタグ）
  > .wp-block-video {
    margin-top: 30px;
    margin-bottom: 30px;
    video {
      width: 100%;
    }
  }

  // 埋め込み（YouTubeやTwitter）
  > .wp-block-embed {
    margin-top: 30px;
    margin-bottom: 30px;
    .wp-block-embed__wrapper {
      text-align: center;
      .twitter-tweet {
        margin: 0 auto;
      }
      iframe {
        // style
      }
    }
  }

  // 引用
  > .wp-block-quote {
    margin-top: 30px;
    margin-bottom: 30px;
    > p {
      @include global.fz(16);
    }
  }

  // 区切り線
  > .wp-block-separator {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .p-entry__modal {
    margin-bottom: global.vw_sp(40);
    @include global.md {
      margin-bottom: 40px;
    }
    .p-entry__modalButton {
      display: flex;
      align-items: center;
      justify-content: center;
      width: global.vw_sp(185);
      height: global.vw_sp(37);
      margin: global.vw_sp(14) auto 0;
      line-height: 1;
      text-transform: uppercase;
      letter-spacing: 0.04em;
      background: global.$color_white;
      border: 1px solid global.$color_border;
      @include global.fz(14);
      @include global.md {
        width: 185px;
        height: 37px;
        margin-top: 18px;
      }
    }
    span {
      display: inline-flex;
      align-items: center;
      &::after {
        width: 8px;
        height: 8px;
        margin-left: 5px;
        content: "";
        background: url("../img/common/icon_plus.svg") no-repeat 50% 50%;
        background-size: 8px auto;
      }
    }
  }
  .c-buttonWrap {
    margin-top: global.vw_sp(59);
    @include global.md {
      margin-top: 50px;
    }
  }
  .p-entryPagination {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: global.vw_sp(34);
    border-top: 1px solid #d5d5d5;
    @include global.lg {
      padding-top: 34px;
    }
    .p-entryPagination__item {
      a {
        position: relative;
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        @include global.font_weight_bold;
        @include global.line_height(18, 16);
        transition: color 0.5s global.$ease_out_quint;
        &::before {
          position: absolute;
          top: calc(50% - global.vw_sp(6));
          right: global.vw_sp(27);
          flex-shrink: 0;
          width: global.vw_sp(7);
          height: global.vw_sp(12);
          content: "";
          background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 7.351 11.872"><path d="M906.948,4498.5l5.229,5.229-5.229,5.229" transform="translate(-906.24 -4497.794)" fill="none" stroke="%23007db7" stroke-width="2"/></svg>');
          background-repeat: no-repeat;
          background-size: contain;
          @include global.md {
            top: calc(50% - 6px);
            right: 27px;
            width: 7px;
            height: 12px;
          }
        }
        @include global.hover {
          color: global.$color_primary;
        }
      }
      &._prev {
        a {
          padding-left: global.vw_sp(17);
          @include global.lg {
            padding-left: 17px;
          }
          &::before {
            left: global.vw_sp(0);
            margin-right: global.vw_sp(10);
            transform: rotate(-180deg);
            @include global.md {
              left: 0;
              margin-right: 10px;
            }
          }
        }
      }
      &._next {
        a {
          padding-right: global.vw_sp(17);
          @include global.lg {
            padding-right: 17px;
          }
          &::before {
            right: 0;
            margin-left: global.vw_sp(10);
            @include global.md {
              right: 0;
              margin-left: 10px;
            }
          }
        }
      }
    }
  }
  .wp-block-button {
    border-radius: 30px;
  }
  .wp-block-button__link {
    display: inline-block;
    min-height: 56px;
    padding: 20px 30px;
    color: global.$color_white;
    background: global.$color_primary;
    box-sizing: border-box;
    line-height: 1;
    text-decoration: none;
    @include global.font_weight_bold;
    @include global.hover_opacity;
    &[href$=".pdf"] {
      &::after {
        display: inline-block;
        width: 14px;
        height: 17px;
        margin-left: 6px;
        background: url("../img/icon_pdf.svg") no-repeat 50% 50%;
        background-size: 14px auto;
        content: "";
      }
    }
    &[target="_blank"] {
      &::after {
        display: inline-block;
        width: 13px;
        height: 11px;
        margin-left: 6px;
        background: url("../img/icon_window.svg") no-repeat 50% 50%;
        background-size: 13px auto;
        content: "";
      }
    }
  }
}

.wp-block-media-text .wp-block-media-text__media {
  @include global.md {
    align-self: start;
  }
}

.wp-block-media-text .wp-block-media-text__content {
  @include global.under_md {
    padding: 0;
  }
  @include global.md {
    align-self: start;
    padding: 0 44px 0 0;
  }
  h3 {
    margin-bottom: global.vw_sp(16);
    letter-spacing: 0.075em;
    @include global.font_weight_bold;
    @include global.line_height(34, 20);
    @include global.fz(20);
    @include global.md {
      margin-bottom: 16px;
    }
  }
}

.wp-block-media-text__media + .wp-block-media-text__content {
  @include global.under_md {
    padding: 0;
  }
  @include global.md {
    padding: 0 0 0 44px;
  }
}

.wp-block-media-text__media .wp-element-caption {
  margin-top: 4px;
  text-align: center;
  @include global.fz(13);
}

.wp-block-group {
  padding-top: global.vw_sp(74);
  padding-bottom: global.vw_sp(90);
  border-image-source: linear-gradient(#fafafa, #fafafa);
  border-image-slice: fill 0;
  border-image-outset: 0 100vw 0 100vw;
  @include global.md {
    padding-top: 95px;
    padding-bottom: 93px;
  }
}
