@charset "utf-8";

@use "../global";

.l-globalNavigation {
  @include global.under_md {
    width: 100%;
    height: 100%;
    padding: 55px 20px 20px;
    overflow-y: auto;
    background-color: global.$color_white;
  }
  @include global.md {
    padding-left: 414px;
  }
  .l-globalNavigation__list {
    @include global.md {
      display: flex;
    }
  }
  .l-globalNavigation__item {
    letter-spacing: 0.05em;
    @include global.fz(16);
    @include global.md {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 72px;
      letter-spacing: 0.1em;
      @include global.fz(15);
    }
    .l-globalNavigation__itemWrap {
      position: relative;
      @include global.under_md {
        .l-globalNavigation__btn {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 3;
          display: flex;
          width: 100%;
          height: 100%;
          padding-right: 3px;
          &::before {
            display: block;
            align-self: center;
            width: 25px;
            height: 25px;
            margin-left: auto;
            content: "";
            background: url(../img/common/icon_arrow_white.png) no-repeat 50% 50%;
            background-size: auto 12px;
            transition: transform 0.3s global.$ease_out_quint;
            transform-origin: center;
          }
          span {
            position: absolute;
            display: block;
            width: 1px;
            height: 1px;
            padding: 0;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
            white-space: nowrap;
            border: 0;
          }
          @at-root .l-footer .l-footerNav__item._open .l-footerNav__btn {
            &::before {
              transform: rotate(90deg);
            }
          }
        }
      }
    }
    &._info {
      @include global.under_md {
        margin-top: 39px;
      }
      .l-globalNavigation__itemLink {
        text-decoration: none;
        @include global.font-JP_bold_sans;
        @include global.under_md {
          padding: 20px 10px 14px;
        }
        @include global.under_md {
          display: block;
          padding: 20px 10px 14px;
          color: global.$color_black;
          text-align: center;
        }
        @include global.md {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 72px;
          padding-right: 24px;
          padding-left: 26px;
          margin-left: 17px;
          transition: background 0.3s global.$ease_out_quint, color 0.3s global.$ease_out_quint;
        }
        @include global.hover {
          color: global.$color_white;
          background: global.$color_primary;
        }
      }
    }
    &:not(._info) {
      @include global.under_md {
        border-bottom: 1px solid global.$color_white;
      }
      .l-globalNavigation__itemLink {
        text-decoration: none;
        @include global.font-JP_bold_sans;
        @include global.under_md {
          display: block;
          padding-top: 17px;
          padding-bottom: 14px;
        }
        @include global.md {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 72px;
          padding-right: 17px;
          padding-left: 17px;
          transition: color 0.3s global.$ease_out_quint;
        }
        @media screen and (min-width: 1025px) and (max-width: 1100px) {
          padding-right: 10px;
          padding-left: 10px;
        }
        @include global.hover {
          color: global.$color_primary;
        }
      }
    }
  }
}

.l-globalNavigationSub,
.l-globalNavigation__btn {
  @include global.md {
    display: none;
  }
}

.l-globalNavigationSub {
  @include global.under_md {
    padding-bottom: 10px;
    padding-left: 18px;
    overflow: hidden;
    transition: height 0.3s ease;
    @at-root .l-footer .l-footerNav__item._open .l-footerSubNav {
      display: block;
    }
  }
  .l-globalNavigationSub__item {
    @include global.under_md {
      a {
        display: block;
        padding: 10px 0;
        text-decoration: none;
      }
    }
    @at-root .l-globalNavigation .l-globalNavigation__item:not(._open) .l-globalNavigationSub {
      display: none;
    }
  }
}
