@charset "utf-8";

@use "../global";

.l-footer {
  position: relative;
  text-align: center;
  @include global.md {
    padding-top: 0;
  }
  .p-contact__finish & {
    margin-top: 30px;
  }
  .l-footer__inner {
    padding-right: 18px;
    padding-left: 18px;
    @include global.md {
      padding-right: 12.66%;
      padding-left: 12.66%;
    }
  }
  .l-footer__link {
    color: global.$color_primary;
    text-align: center;
    letter-spacing: 0.025em;
    @include global.fz(14);
    @include global.line_height(24, 14);
    @include global.font-JP_bold_sans;
    a {
      color: global.$color_primary;
      &[target="_blank"] span {
        display: inline-block;
        padding-right: 16px;
        background: url("../img/icon_window.svg") no-repeat 100% 50%;
        background-size: 9px auto;
      }
      @include global.hover_opacity;
    }
  }
  .l-footer__copy {
    display: block;
    padding: global.vw_sp(32) 0 global.vw_sp(28);
    margin-top: 17px;
    color: global.$color_white;
    text-align: center;
    letter-spacing: 0.11em;
    background: transparent linear-gradient(90deg, #3550E5 0%, #85D1E3 100%) 0 0 no-repeat padding-box;
    @include global.font-EN;
    @include global.line_height(18, 12);
    @include global.fz(12);
    @include global.md {
      padding: 40px 0 36px;
      @include global.line_height(18, 14);
      @include global.fz(14);
    }
  }
}
