@charset "utf-8";

@use "sass:math";
@use "../global";

.c-flow {
  padding-top: 60px;
  @include global.md {
    padding-top: 102px;
    padding-bottom: 57px;
  }
  .c-flow__inner {
    width: 100%;
    margin: 0 auto;
    @include global.md {
      display: flex;
      justify-content: space-between;
    }
  }
  .c-flow__list {
    &:nth-of-type(1) {
      .c-flow__item {
        &:first-child {
          @include global.under_md {
            padding-top: 30px;
            &::before {
              top: 0;
            }
          }
        }
        &:last-child {
          @include global.under_md {
            &::before {
              background: #d8d8d8 url("../img/line_flow.svg") no-repeat 0 100%;
              background-size: 6px auto;
            }
          }
          @include global.md {
            .c-flow__wrap::before {
              background: #d8d8d8 url("../img/line_flow.svg") no-repeat 0 100%;
              background-size: 6px auto;
            }
          }
        }
      }
    }
    &:nth-of-type(2) {
      .c-flow__item {
        &:nth-child(1) {
          @include global.under_md {
            padding-top: 30px;
            &::before {
              top: 0;
              background: #d8d8d8 url("../img/line_flow.svg") no-repeat 0 0;
              background-size: 6px auto;
            }
          }
          @include global.md {
            .c-flow__wrap::before {
              background: #d8d8d8 url("../img/line_flow.svg") no-repeat 0 0;
              background-size: 6px auto;
            }
          }
        }
      }
    }
    @include global.under_md {
      padding: 0 38px;
    }
    @include global.md {
      width: global.per_w_pc(478, 1066);
    }
    & + .c-flow__list {
      @include global.under_md {
        margin-top: global.vw_sp(50);
      }
    }
  }
  .c-flow__item {
    @include global.under_md {
      position: relative;
    }
    @include global.md {
      display: flex;
    }
    &::before {
      @include global.under_md {
        position: absolute;
        left: -20px;
        width: 6px;
        height: calc(100% + 20px);
        content: "";
        background: #D8D8D8;
      }
    }
    &:nth-child(1) {
      @include global.sm {
        .c-flow__image {
          &::before {
            height: calc(100% + 20px);
          }
        }
      }
      @include global.under_md {
        .c-flow__image {
          position: relative;
          &::before {
            position: absolute;
            top: 0;
            left: -22px;
            display: block;
            width: 5px;
            height: calc(100% + 30px);
            content: "";
            background: global.$color_white;
          }
        }
      }
      @include global.md {
        .c-flow__wrap {
          padding-top: 30px;
        }
        .c-flow__wrap::before {
          top: 0;
        }
      }
    }
    &:nth-last-child(1) {
      @include global.under_md {
        .c-flow__wrap {
          padding-bottom: 30px;
        }
        .c-flow__wrap::before {
          bottom: 0;
        }
      }
    }
    + .c-flow__item {
      margin-top: 20px;
      @include global.md {
        margin-top: 20px;
      }
    }
  }
  .c-flow__wrap {
    position: relative;
    display: flex;
    @include global.md {
      padding-left: 54px;
    }
    &::before {
      @include global.md {
        position: absolute;
        left: 34px;
        width: 6px;
        height: calc(100% + 24px);
        content: "";
        background: #d8d8d8;
      }
    }
  }
  .c-flow__time {
    position: relative;
    display: block;
    flex-shrink: 0;
    width: global.vw_sp(74);
    color: global.$color_primary;
    letter-spacing: 0.025em;
    @include global.font-EN(700);
    @include global.line_height(29, 20);
    @include global.fz(20);
    @include global.md {
      @include global.line_height(29, 22);
      @include global.fz(22);
    }
    @include global.md {
      width: 87px;
    }
    &::before {
      position: absolute;
      left: -26px;
      display: inline-block;
      width: 17px;
      height: 17px;
      margin-top: 0.4em;
      content: "";
      background: global.$color_primary;
      border-radius: 10px;
    }
  }
  .c-flow__heading {
    position: relative;
    display: inline-block;
    margin-top: -0.4em;
    margin-bottom: 6px;
    letter-spacing: 0.075em;
    border-bottom: 2px solid global.$color_primary;
    @include global.line_height(20, 16);
    @include global.fz(16);
    @include global.md {
      margin-bottom: 15px;
      @include global.line_height(34, 18);
      @include global.fz(18);
    }
  }
  .c-flow__text {
    letter-spacing: 0.025em;
    @include global.line_height(22, 14);
    @include global.md {
      @include global.line_height(26, 16);
    }
  }
  .c-cta {
    @include global.md {
      padding-bottom: 42px;
    }
  }
}
