@charset "utf-8";

@use "../global";

[class*="c-heading"] {
  color: global.$color_black;
  &._center {
    text-align: center;
  }
  &._right {
    text-align: right;
  }
  &._left {
    text-align: left;
  }
  &:first-child {
    margin-top: 0;
  }
}

.c-heading__wrap {
  .l-contents__wide & {
    max-width: global.$content_width_narrow;
    margin-right: auto;
    margin-left: auto;
  }
}

.c-heading01 {
  position: relative;
  padding-bottom: global.vw_sp(20);
  margin-bottom: global.vw_sp(40);
  border-bottom: 2px solid global.$color_border;
  letter-spacing: 0.075em;
  @include global.font_weight_bold;
  @include global.line_height(34, 20);
  @include global.fz(20);
  @include global.md {
    padding-bottom: 20px;
    margin-bottom: 40px;
    @include global.line_height(34, 22);
    @include global.fz(22);
  }
  &::after {
    position: absolute;
    left: 0;
    bottom: -2px;
    display: block;
    width: global.vw_sp(80);
    height: 2px;
    content: "";
    background: global.$color_primary;
    @include global.md {
      width: 80px;
    }
  }
}

.c-heading02 {
  text-align: center;
  margin-bottom: global.vw_sp(50);
  @include global.font_weight_bold;
  @include global.line_height(34, 20);
  @include global.fz(20);
  @include global.md {
    margin-bottom: 50px;
    @include global.line_height(34, 22);
    @include global.fz(22);
  }
  &::after {
    display: block;
    width: global.vw_sp(47);
    height: 4px;
    margin: global.vw_sp(24) auto 0;
    content: "";
    background: global.$color_primary;
    @include global.md {
      width: 47px;
      margin-top: 24px;
    }
  }
}

.c-heading03 {
  margin-bottom: 40px;
  letter-spacing: 0.075em;
  @include global.font_weight_bold;
  @include global.line_height(34, 18);
  @include global.fz(18);
  @include global.md {
    margin-bottom: 40px;
    @include global.line_height(34, 20);
    @include global.fz(20);
  }
}

.c-heading04 {
  margin-bottom: global.vw_sp(16);
  @include global.font_weight_bold;
  @include global.line_height(34, 18);
  @include global.fz(18);
  @include global.md {
    @include global.line_height(34, 20);
    @include global.fz(20);
  }
  @include global.md {
    margin-bottom: 16px;
  }
  &::before {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 8px;
    content: "";
    background: #b6d2ed;
  }
}

.c-heading05 {
  margin-bottom: 10px;
  color: global.$color_primary;
  @include global.md {
    margin-bottom: 23px;
  }
  span {
    display: inline-flex;
    @include global.fz(24);
    @include global.md {
      letter-spacing: 0.01em;
      @include global.fz(28);
    }
    &::before {
      display: inline-block;
      width: 20px;
      height: 4px;
      margin-top: 0.7em;
      margin-right: 10px;
      content: "";
      background: global.$color_primary;
    }
  }
}

.c-heading06 {
  margin-bottom: 45px;
  line-height: 1.2;
  text-align: center;
  @include global.md {
    margin-top: 24px;
    letter-spacing: 0.01em;
    @include global.fz(24);
  }
  &::after {
    display: block;
    width: 43px;
    height: 3px;
    margin: 15px auto 0;
    content: "";
    background: global.$color_primary;
    @include global.md {
      width: 60px;
      height: 4px;
      margin-top: 30px;
    }
  }
}

.c-heading07 {
  padding-bottom: 15px;
  margin-bottom: 26px;
  letter-spacing: 0.04em;
  border-bottom: 1px solid #cecece;
  @include global.fz(20);
  @include global.md {
    letter-spacing: 0.08em;
  }
}

.c-headingCheck {
  margin-bottom: 30px;
  line-height: 1.2;
  @include global.md {
    margin-bottom: 40px;
    letter-spacing: 0.01em;
    @include global.fz(24);
  }
}
