@charset "utf-8";

@use "../global";

.c-mainVisual {
  @include global.md {
    height: global.vw(320);
  }
  .c-mainVisual__inner {
    height: 100%;
    background: url("../img/news_mainvisual_sp.jpg") no-repeat 50% 0;
    background-size: auto global.vw_sp(178);
    @include global.under_md {
      padding-top: global.vw_sp(178);
    }
    @include global.md {
      background: url("../img/news_mainvisual_pc.jpg") no-repeat 100% 50%;
      background-size: calc(100% - global.vw(440)) auto;
    }
  }
  .c-mainVisual__title {
    display: flex;
    align-items: center;
    width: 100%;
    height: global.vw_sp(130);
    padding-left: global.vw(95);
    background: url("../img/news_mainvisual_bg_pc.jpg") no-repeat 0 0;
    background-size: 100% auto;
    letter-spacing: 0.075em;
    @include global.font_weight_bold;
    @include global.line_height(47, 26);
    @include global.fz(26);
    @include global.md {
      width: global.vw(440);
      height: 100%;
      @include global.line_height(47, 34);
      @include global.fz(34);
    }
    span {
      &::after {
        display: block;
        color: global.$color_primary;
        letter-spacing: 0.05em;
        content: attr(data-en-text);
        @include global.font-EN;
        @include global.line_height(20, 14);
        @include global.fz(14);
        @include global.md {
          margin-top: 8px;
          @include global.line_height(20, 16);
          @include global.fz(16);
        }
      }
    }
  }
}
