@charset "utf-8";

@use "sass:math";
@use "../global";

.c-select {
  position: relative;
  display: inline-block;
  overflow: hidden;
  @include global.lg {
    width: 350px;
    &:hover {
      &::after {
        border-color: global.$color_primary;
      }
    }
  }
  &::before {
    position: absolute;
    top: calc(50% - 10px);
    right: 18px;
    z-index: 2;
    width: 10px;
    height: 21px;
    cursor: pointer;
    content: "";
    background: url("../img/icon_select_arrow.svg") no-repeat 0 0;
    background-size: cover;
  }
  select {
    display: block;
    width: 100%;
    height: 50px;
    padding: 10px 60px 10px 24px;
    line-height: math.div(32, 16);
    cursor: pointer;
    background: global.$color_white;
    border: 1px solid #d5d5d5;
    appearance: none;
    @include global.fz(14);
    border-radius: 6px;
    &[disabled] {
      cursor: not-allowed;
    }
    &:focus {
      border-color: global.$color_primary;
      border-width: 2px;
    }
  }
  select::-ms-expand {
    display: none;
  }
}
