@charset "utf-8";

@use "../global";

.c-faq {
  .c-faq__heading {
    color: global.$color_primary;
    text-align: center;
    letter-spacing: 0.05em;
    @include global.fz(30);
    @include global.under_lg {
      margin-bottom: 46px;
      text-align: center;
    }
    @include global.lg {
      margin-bottom: 56px;
      @include global.fz(34);
    }
  }
  .c-faq__item {
    + .c-faq__item {
      margin-top: 5px;
    }
  }
  .c-faq__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    padding: 15px 40px 15px 20px;
    text-align: left;
    letter-spacing: 0.025em;
    background: #f4f4f4;
    @include global.font_weight_bold;
    @include global.fz(18);
    @include global.line_height(24, 18);
    @include global.md {
      padding: 23px 94px 21px 28px;
      transition: background 0.3s ease, color 0.3s ease;
      &:hover {
        background-color: #f2f8fd;
      }
    }
    &::before {
      display: inline-block;
      align-self: flex-start;
      margin-right: 4px;
      color: global.$color_primary;
      content: "Q";
      @include global.font-EN(700);
      @include global.fz(22);
    }
    span {
      position: absolute;
      right: 20px;
      display: block;
      width: global.vw_sp(10);
      height: global.vw_sp(6);
      margin-left: 10px;
      overflow: hidden;
      line-height: 500px;
      content: "";
      background: url("../img/angle_brackets_l.svg") no-repeat 0 0;
      background-size: global.vw_sp(10) auto;
      transition: transform 0.3s ease, background 0.3s ease;
      @include global.md {
        width: 10px;
        height: 6px;
        background-size: 10px auto;
      }
      @at-root .p-top .c-faq .c-faq__item._open .c-faq__btn span {
        transform: rotate(180deg);
      }
    }
  }
  .c-faq__body {
    overflow: hidden;
    transition: height 0.3s ease;
    @at-root .c-faq .c-faq__item:not(._open) .c-faq__body {
      display: none;
    }
  }
  .c-faq__bodyInner {
    padding: 15px 20px;
    @include global.lg {
      padding: 34px 22px;
    }
  }
}
