@charset "utf-8";

@use "../global";

.c-grid {
  display: grid;
  counter-reset: cardNo 0;
  &._archives {
    @include global.under_md {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: global.vw_sp(25) global.vw_sp(17);
    }
    @include global.md {
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 44px 30px;
    }
  }
  &._col2 {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: global.vw_sp(25) global.vw_sp(17);
    @include global.md {
      grid-gap: 40px 25px;
    }
  }
  &._col3 {
    @include global.under_md {
      display: block;
    }
    @include global.md {
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 40px 25px;
    }
    .c-grid__item {
      & + .c-grid__item {
        @include global.under_md {
          margin-top: global.vw_sp(32);
        }
      }
    }
  }
  &._shadow {
    @include global.under_md {
      display: block;
    }
    .c-grid__item {
      background: global.$color_white;
      box-shadow: 0 3px 12px rgba(0, 0, 0, 0.16);
      & + .c-grid__item {
        @include global.under_md {
          margin-top: global.vw_sp(32);
        }
      }
    }
  }
}
