@charset "utf-8";

@use "breakpoint";
@use "variables";
@use "sass:math";

// font-family
@mixin font-JP_sans {
  font-family: "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", sans-serif;
}
@mixin font-JP_bold_sans($weight: 700) {
  font-family: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;
  font-weight: $weight;
}
@mixin font-EN($weight: 400) {
  font-family: 'Barlow', sans-serif;
  font-weight: $weight;
}

// font-size
@mixin fz($size, $base: 16) {
  font-size: $size + px;
  font-size: math.div($size, $base) + rem;
}

// font-weight
@mixin font_weight_bold {
  font-weight: 700;
}

// line-height
@mixin line_height($line, $fz) {
  line-height: math.div($line, $fz);
}

// コンテンツ幅
// 引数にwide || narrowを指定
@mixin content_width($width) {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  @if $width == wide {
    max-width: variables.$content_width;
  } @else if $width == narrow {
    max-width: variables.$content_width_narrow;
  }
}

// function
@function vw($target, $base: variables.$de_full) {
  @return (math.div($target, $base) * 100vw);
}
@function vw_sp($target) {
  @return (math.div($target, variables.$de_md) * 100vw);
}
@function per_w_pc($width, $base: variables.$de_lg) {
  @return math.div($width, $base) * 100%;
}

@mixin aspect($width, $height) {
  position: relative;
  @include clearfix;
  &::before {
    float: left;
    padding-top: math.div($height, $width) * 100%;
    content: "";
  }
  & > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

// object-fit
// @include object-fit;
@mixin object_fit($fit: cover, $position: null) {
  object-fit: $fit;
  @if $position {
    object-position: $position;
    font-family: "object-fit: #{$fit}; object-position: #{$position}";
  } @else {
    font-family: "object-fit: #{$fit}";
  }
}

// @include clearfix;
@mixin clearfix {
  &::after {
    display: block;
    clear: both;
    content: "";
  }
}

// 横幅 px → % 変換
@function vw($target) {
  @return (math.div($target, 1500) * 100vw);
}
@function vw-sp($target) {
  @return (math.div($target, 375) * 100vw);
}
@function per_w_pc($width, $base: 1240) {
  @return math.div($width, $base) * 100%;
}

// font-size可変 SP
// @include fzVariableSP(px);
@mixin fzVariableSP($fz) {
  font-size: ($fz / 375 /*SPデザイン横幅*/) * 100vw;
}

// font-size可変 PC
// @include fzVariablePC(px);
@mixin fzVariablePC($fz) {
  font-size: ($fz / 1200 /*PCデザイン横幅*/) * 100vw;
}

// ブラウザハック

// Mozilla Firefox
// @include only_firefox {}
@mixin only_firefox {
  @supports (-moz-appearance: meterbar) {
    @content;
  }
}

// Safari
// @include only_safari {}
@mixin only_safari {
  @supports (paint-order: fill) and (-webkit-marquee-speed: 0) {
    @content;
  }
}

// iOS Safari
// @include only_iOS {}
@mixin only_iOS {
  @supports (-webkit-text-size-adjust: none) and (not (-ms-accelerator: true)) and (not (-moz-appearance: none)) {
    @content;
  }
}

// Microsoft Edge
// @include only_edge {}
@mixin only_edge {
  @supports (-ms-ime-align: auto) {
    @content;
  }
}

// Internet Explorer
// @include only_IE {}
@mixin only_IE {
  /* stylelint-disable-next-line selector-type-no-unknown */
  _:-ms-input-placeholder,
  :root & {
    @content;
  }
}

// @include not_IE {}
@mixin not_IE {
  @supports (appearance: none) {
    @content;
  }
}
