@charset "utf-8";

@use "../global";

.c-table {
  > table {
    margin-top: 35px;
    margin-bottom: 21px;
    background: global.$color_white;
    border-top: 1px solid #cecece;
    border-right: 1px solid #cecece;
    @include global.md {
      width: 100%;
      margin-top: 46px;
      margin-bottom: 21px;
    }
    & + table {
      margin-top: 35px;
      @include global.md {
        margin-top: 46px;
      }
    }
  }
  th,
  td {
    padding: 15px 14px 15px;
    border-bottom: 1px solid #C3C3C3;
    border-left: 1px solid #C3C3C3;
    @include global.md {
      padding: 25px 30px;
    }
  }
  th {
    text-align: left;
    letter-spacing: 0.01em;
    vertical-align: top;
    background: #F2F8FD;
    @include global.font_weight_bold;
    @include global.line_height(40, 28);
    @include global.md {
      letter-spacing: 0.02em;
      @include global.line_height(28, 16);
    }
  }
  thead th {
    background: #C0D6E8;
  }
  td {
    > p {
      @include global.line_height(40, 28);
      @include global.md {
        @include global.line_height(28, 16);
      }
    }
  }
  &._line {
    table {
      border-right: none;
    }
    th,
    td {
      border-left: none;
    }
    th {
      background: none;
    }
  }
  .p-recruitEmployee & {
    @include global.under_md {
      table,
      tbody,
      tr,
      th,
      td {
        display: block;
        width: 100%;
      }
    }
  }
}
