@charset "utf-8";

@use "sass:math";
@use "../global";

.c-list {
  a {
    text-decoration: none;
    @include global.hover {
      text-decoration: underline;
    }
  }
  .c-list__time {
    color: #b7b7b7;
    letter-spacing: 0.02em;
    white-space: nowrap;
    @include global.line_height(20, 14);
    @include global.font-EN(500);
    @include global.md {
      @include global.line_height(20, 16);
    }
  }
  .c-list__itemInner {
    padding-bottom: 23px;
    border-bottom: 1px solid #e8e8e8;
  }
  &:not(._date) {
    .c-list__detail {
      margin-top: 8px;
    }
  }
  &._date {
    .c-list__item {
      &:not(:first-of-type) {
        margin-top: 30px;
        @include global.md {
          margin-top: 28px;
        }
      }
      > a,
      .c-list__itemInner {
        > .c-list__detail {
          @include global.font_weight_bold;
        }
      }
      .c-list__time {
        display: block;
        margin-bottom: 3px;
      }
    }
  }
}

.c-items {
  .l-header__globalNav & {
    @include global.md {
      margin-top: 23px;
    }
  }
  .c-items__heading {
    margin-bottom: 4px;
    @include global.line_height(20, 14);
    @include global.fz(14);
  }
  .c-itemsList__item {
    margin-top: 3px;
    a {
      display: flex;
      text-decoration: none;
      text-transform: uppercase;
      letter-spacing: 0.14em;
      @include global.hover {
        text-decoration: underline;
      }
      &::before {
        position: relative;
        top: 0.9em;
        display: inline-block;
        flex-shrink: 0;
        margin-right: 5px;
        width: 4px;
        height: 7px;
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 6.108 10.803"><path fill="none" stroke="%23535353" d="M4946,1227.313l5.048,5.048-5.048,5.048" transform="translate(-4945.646 -1226.959)" stroke-width="1" /></svg>');
        background-size: 4px auto;
        content: "";
        .l-header__globalNav &,
        .l-footerNav & {
          background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 6.108 10.803"><path fill="none" stroke="%23535353" d="M4946,1227.313l5.048,5.048-5.048,5.048" transform="translate(-4945.646 -1226.959)" stroke-width="1" /></svg>');
        }
      }
    }
  }
}
