@charset "utf-8";

@use "breakpoint";
@use "mixin";
@use "variables";

// hover
@mixin hover {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}

// hover opacity
@mixin hover_opacity($opacity: 0.5, $duration: 0.3s) {
  @media (any-hover: hover) {
    transition: opacity $duration variables.$ease_out_quint;
  }
  @include hover {
    opacity: $opacity;
  }
}

//hover button
@mixin hover_button($color: variables.$color_white, $base: variables.$color_primary) {
  @include breakpoint.md {
    position: relative;
    overflow: hidden;
    span {
      position: relative;
      z-index: 3;
      transition: color 0.5s variables.$ease_out_quint;
    }
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      content: "";
      background: ($base);
      transition: transform 0.5s variables.$ease_out_quint;
      transform: scale(0, 1);
      transform-origin: right top;
    }
    @include hover {
      span {
        color: ($color);
      }
      &::before {
        transform: scale(1, 1);
        transform-origin: left top;
      }
    }
  }
}

//hover 画像拡大
@mixin hover_zoom {
  @media (any-hover: hover) {
    position: relative;
    z-index: 1;
    overflow: hidden;
    img {
      transition: transform 0.5s variables.$ease_out_quint;
    }
    @include hover {
      img {
        transform: scale(1.05);
      }
    }
  }
}

