@charset "utf-8";

@use "../global";

.p-topMainVisual {}

.p-top {
  .p-topIntro {
    position: relative;
    z-index: 2;
    @include global.under_lg {
      padding: global.vw-sp(48) 18px 24px;
    }
    @include global.lg {
      padding-top: 20px;
    }
    &::after {
      position: absolute;
      bottom: -50px;
      left: calc(50% - 53px);
      width: 0;
      height: 0;
      content: "";
      border-color: global.$color_white transparent transparent transparent;
      border-style: solid;
      border-width: 50px 53px 0 53px;
      @include global.lg {
        bottom: -56px;
        left: calc(50% - 66.5px);
        border-width: 56px 66.5px 0 66.5px;
      }
    }
    .p-topIntro__lead {
      box-sizing: border-box;
      padding: 0 17px;
      margin: 0 auto;
      border: 3px solid #e5e5e5;
      @include global.under_lg {
        max-width: 550px;
        margin-bottom: 30px;
      }
      @include global.lg {
        width: 763px;
        padding: 0 17px;
      }
      span {
        display: block;
        padding: 16px 0;
        margin: -5px 0;
        color: global.$color_primary;
        text-align: center;
        background: global.$color_white;
        @include global.font_weight_bold;
        @include global.line_height(35, 21);
        @include global.fz(21);
        @include global.lg {
          padding: 12px 20px;
          letter-spacing: 0.05em;
          @include global.line_height(56, 34);
          @include global.fz(34);
        }
        em {
          font-style: normal;
          color: global.$color_secondary;
          @include global.font_weight_bold;
        }
      }
    }
    .p-topIntro__image {
      display: block;
      text-align: center;
      @include global.lg {
        padding-bottom: 15px;
        margin-top: -7px;
      }
      img {
        @include global.lg {
          width: 621px;
        }
      }
    }
  }
  .p-topFeature {
    position: relative;
    z-index: 1;
    padding-top: 90px;
    background: url("../img/feature_bg_sp.jpg") no-repeat;
    background-size: 100% auto;
    @include global.lg {
      padding-top: 94px;
      padding-bottom: 107px;
      background: url("../img/feature_bg_pc.jpg") no-repeat;
      background-size: cover;
    }
    .p-topFeature__contents {
      @include global.lg {
        width: 100%;
        max-width: global.$content_width_narrow;
        padding: 0 20px;
        margin: 0 auto;
      }
      .p-topFeature__contentsInner {
        @include global.lg {
          position: relative;
        }
      }
      h2 {
        margin-bottom: 47px;
        color: global.$color_primary;
        text-align: center;
        letter-spacing: 0.05em;
        @include global.fz(30);
        @include global.font-JP_bold_sans;
        @include global.lg {
          margin-bottom: 59px;
          @include global.fz(35);
        }
        span {
          letter-spacing: 0;
          @include global.font-EN;
          @include global.under_lg {
            display: block;
            @include global.fz(32);
          }
          @include global.lg {
            @include global.fz(36);
          }
        }
      }
    }
    .p-topFeature__list {
      @include global.under_lg {
        padding: 0 18px 69px;
      }
      @include global.lg {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 36px 36px;
      }
    }
    .p-topFeature__item {
      background: global.$color_white;
      box-shadow: 0 3px 12px rgba(0, 0, 0, 0.06);
      @include global.under_lg {
        padding: 0 20px 47px;
        + .p-topFeature__item {
          margin-top: 30px;
        }
      }
      @include global.lg {
        padding: 0 33px 73px;
      }
    }
    .p-topFeature__image {
      box-sizing: border-box;
      margin: 0 auto;
      text-align: center;
      @at-root .p-top .p-topFeature .p-topFeature__item:nth-of-type(3) .p-topFeature__image {
        position: relative;
        top: 10px;
      }
      @include global.under_md {
        height: 52vw;
        padding-top: 10.4vw;
        img {
          width: global.vw-sp(163);
          height: auto;
        }
      }
      @include global.md {
        height: 49vw;
        padding-top: 8vw;
        img {
          width: global.vw-sp(163);
        }
      }
      @include global.lg {
        height: 208px;
        padding-top: 53px;
        img {
          width: initial;
        }
      }
    }
    .p-topFeature__body {
      h3 {
        margin-bottom: 17px;
        color: global.$color_primary;
        text-align: center;
        letter-spacing: 0.05em;
        @include global.fz(17);
        @include global.font-JP_bold_sans;
        @include global.lg {
          @include global.fz(20);
          margin-bottom: 15px;
        }
      }
      .p-topFeature__lead {
        text-align: justify;
        letter-spacing: 0.025em;
        @include global.line_height(28, 15);
        @include global.fz(15);
      }
    }
  }
  .p-topMessage {
    padding-top: 60px;
    padding-bottom: 85px;
    background: linear-gradient(#f0fcff 0%, #fff 47.29%, #ebf9ff 100%);
    @include global.lg {
      padding-top: 107px;
      padding-bottom: 148px;
    }
    .l-contents__narrow {
      @include global.under_lg {
        padding: 0 18px;
      }
    }
    .p-topMessage__heading {
      margin-bottom: 25px;
      @include global.lg {
        max-width: global.$content_width_narrow;
        padding: 0 20px;
        margin: 0 auto 50px;
      }
      .p-topMessage__headingTitle {
        color: global.$color_primary;
        text-align: center;
        letter-spacing: 0.05em;
        @include global.fz(30);
        @include global.font-JP_bold_sans;
        @include global.lg {
          @include global.fz(34);
        }
      }
    }
    .p-topMessage__lead {
      width: 100%;
      max-width: 1000px;
      margin: 0 auto;
      letter-spacing: 0.025em;
      @include global.line_height(24, 17);
      @include global.fz(17);
      @include global.lg {
        text-align: center;
      }
    }
    .p-topMainVisual__icons {
      display: block;
      text-align: center;
      @include global.under_lg {
        margin-top: 24px;
        img {
          width: global.vw-sp(340);
          margin: 0 auto;
        }
      }
      @include global.lg {
        margin-top: 39px;
      }
    }
    .p-topMessage__description {
      margin-top: 17px;
      letter-spacing: 0.025em;
      @include global.line_height(30, 16);
      @include global.fz(16);
      @include global.lg {
        margin-top: 34px;
        text-align: center;
        @include global.line_height(38, 16);
      }
    }
  }
  .p-topProduct {
    padding-top: 61px;
    padding-bottom: 80px;
    @include global.lg {
      padding-top: 107px;
      padding-bottom: 108px;
    }
    .p-topProduct__heading {
      @include global.lg {
        max-width: global.$content_width_narrow;
        padding: 0 20px;
        margin: 0 auto;
      }
      .p-topProduct__headingTitle {
        margin-bottom: 20px;
        color: global.$color_primary;
        text-align: center;
        letter-spacing: 0.05em;
        @include global.fz(30);
        @include global.font-JP_bold_sans;
        @include global.lg {
          @include global.fz(34);
        }
      }
    }
    .p-topProduct__list {
      @include global.under_lg {
        padding-top: global.vw-sp(135);
      }
      @include global.lg {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 36px 36px;
        padding-top: 140px;
      }
    }
    .p-topProduct__item {
      position: relative;
      display: flex;
      flex-direction: column;
      padding: 0 18px 32px;
      @include global.lg {
        padding: 0 20px 37px;
      }
      &._kiosk {
        .product__image {
          margin-top: global.vw-sp(-117);
          @include global.lg {
            margin-top: -117px;
          }
          img {
            @include global.under_lg {
              width: global.vw-sp(107);
              max-width: 214px;
            }
          }
        }
        .product__detail .product__detailItem dt {
          color: #007db7;
          background: #ebf9ff;
        }
        .product__lead .product__leadBtn {
          background: linear-gradient(0.75turn, #007db7 0%, #45a8d6 100%, #007db7 100%);
        }
      }
      &._desktop {
        border: 2px solid global.$color_primary;
        @include global.under_lg {
          margin-top: global.vw-sp(118);
        }
        .product__image {
          margin-top: global.vw-sp(-85);
          @include global.lg {
            margin-top: -85px;
          }
          img {
            @include global.under_lg {
              width: global.vw-sp(232);
              max-width: 464px;
            }
          }
        }
        .product__detail .product__detailItem dt {
          color: global.$color_primary;
          background: #def2f4;
        }
      }
      &._pos {
        @include global.under_lg {
          margin-top: global.vw-sp(110);
        }
        .product__heading {
          margin-top: 16px;
          @include global.lg {
            margin-top: 17px;
          }
        }
        .product__image {
          margin-top: global.vw-sp(-77);
          @include global.lg {
            margin-top: -77px;
          }
          img {
            @include global.under_lg {
              width: global.vw-sp(241);
              max-width: 482px;
            }
          }
        }
        .product__detail .product__detailItem dt {
          color: #1aa692;
          background: #e5f5e1;
        }
        .product__lead .product__leadBtn {
          background: linear-gradient(0.25turn, #4da89c 0%, #217165 100%);
        }
      }
    }
    .product__heading {
      align-self: center;
      order: 2;
      margin: 30px 0;
      @include global.lg {
        margin: 30px 0 27px;
      }
    }
    .product__image {
      position: relative;
      z-index: 2;
      align-self: center;
      order: 1;
    }
    .product__detail {
      order: 3;
      line-height: 1.3;
      letter-spacing: 0.05em;
      @include global.fz(13);
      .product__detailItem {
        display: grid;
        grid-template-rows: auto 1fr;
        grid-template-columns: 111px auto;
        gap: 6px 12px;
        align-items: center;
        dt {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 26px;
          line-height: 1;
          border-radius: 13px;
          @include global.font_weight_bold;
        }
      }
    }
    .product__description {
      order: 4;
      padding-bottom: 23px;
      margin-top: 12px;
      letter-spacing: 0.05em;
      @include global.line_height(28, 14);
      @include global.fz(14);
      @include global.lg {
        margin-top: 20px;
      }
    }
    .product__lead {
      order: 5;
      @include global.lg {
        margin: auto 5px 0;
      }
      .product__leadBtn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 65px;
        padding-right: 20px;
        padding-left: 20px;
        color: global.$color_white;
        text-decoration: none;
        border-radius: 8px;
        @include global.font-JP_bold_sans;
        @include global.fz(16);
        @include global.hover_opacity;
        @include global.under_lg {
          margin-bottom: 8px;
        }
        @include global.lg {
          padding-right: 25px;
          padding-left: 25px;
        }
        span {
          display: inline-block;
          padding-right: 18px;
          background: url("../img/icon_window_white.svg") no-repeat 100% 50%;
          background-size: 14px auto;
          @include global.lg {
            padding-right: 23px;
            background-size: 13px auto;
          }
        }
      }
      .product__leadCurrent {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 80px;
        padding: 0 10px;
        color: global.$color_primary;
        border: 5px solid #ededed;
        @include global.fz(16);
        @include global.under_lg {
          @include global.line_height(23, 16);
        }
        &::before {
          width: 16px;
          height: 10px;
          margin-right: 10px;
          content: "";
          background: url("../img/icon_arrow_double.svg") no-repeat 0 0;
          background-size: 14px auto;
        }
      }
    }
  }
  .p-topCase {
    padding-top: 62px;
    padding-bottom: 70px;
    background: #f8f8f8;
    @include global.under_md {
      padding-right: 18px;
      padding-left: 18px;
    }
    @include global.md {
      padding-top: 102px;
      padding-bottom: 105px;
    }
    .p-topCase__heading {
      color: global.$color_primary;
      text-align: center;
      letter-spacing: 0.05em;
      @include global.fz(30);
      @include global.font-JP_bold_sans;
      @include global.under_md {
        margin-bottom: 24px;
        text-align: center;
      }
      @include global.md {
        margin-bottom: 40px;
        @include global.fz(34);
      }
    }
    .p-topCase__lead {
      color: global.$color_secondary;
      text-align: center;
      letter-spacing: 0.05em;
      @include global.font_weight_bold;
      @include global.line_height(34, 22);
      @include global.fz(22);
      @include global.md {
        @include global.line_height(56, 24);
        @include global.fz(24);
      }
    }
    .p-topCase__images {
      display: block;
      margin-top: 34px;
      text-align: center;
      @include global.under_md {
        padding: 0 10px;
      }
      @include global.md {
        margin-top: 24px;
        img {
          width: 490px;
        }
      }
    }
    .p-topCase__description {
      margin-top: 46px;
      text-align: center;
      letter-spacing: 0.05em;
      @include global.line_height(27, 16);
      @include global.fz(16);
      @include global.md {
        margin-top: 50px;
      }
    }
    .p-topCase__btn {
      margin-top: 16px;
      @include global.md {
        margin: 42px auto 0;
        width: global.vw(780);
      }
      @include global.lg {
        width: global.vw(580);
      }
    }
  }
}
