@charset "utf-8";

@use "../global";

.c-card {
  .c-card__wrap {
    display: grid;
    text-decoration: none;
  }
  a.c-card__wrap {
    @include global.hover_zoom;
    .c-card__body {
      transition: opacity 0.3s global.$ease_out_quint;
    }
    @include global.hover {
      .c-card__body {
        opacity: 0.5;
      }
    }
  }
  .c-card__body {
    order: 2;
    margin-top: global.vw_sp(18);
    @include global.md {
      margin-top: 18px;
    }
    ._shadow .c-grid__item & {
      margin-top: 0;
      padding: global.vw_sp(10) global.vw_sp(20);
      @include global.md {
        padding: 10px 20px;
      }
    }
    .c-buttonWrap {
      margin-top: global.vw_sp(24);
      @include global.md {
        margin-top: 24px;
      }
    }
  }
  .c-cardHeading__no {
    display: flex;
    @include global.font_weight_bold;
    @include global.line_height(22, 14);
    @include global.md {
      @include global.line_height(26, 16);
    }
    .c-icon__no {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      background: #f2f8fd;
      font-style: normal;
      &::before {
        color: global.$color_primary;
        content: counter(cardNo);
        counter-increment: cardNo;
        @include global.font-EN(600);
      }
    }
  }
  .c-cardHeading__title {
    @include global.font_weight_bold;
    @include global.line_height(31, 18);
    @include global.fz(18);
    @include global.md {
      @include global.line_height(34, 20);
      @include global.fz(20);
    }
  }
  .c-cardHeading__text {
    @include global.line_height(22, 14);
    @include global.md {
      @include global.line_height(26, 16);
    }
  }
  .c-card__thumbnail {
    order: 1;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      @include global.object_fit(cover);
      transition: transform 0.5s ease;
    }
  }
}


.c-cardArchive {
  .c-cardArchive__wrap {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    @include global.hover_zoom;
  }
  a.c-cardArchive__wrap {
    .c-cardArchive__body {
      transition: opacity 0.3s global.$ease_out_quint;
    }
    @include global.hover {
      .c-cardArchive__body {
        opacity: 0.5;
      }
    }
  }
  .c-cardArchive__body {
    order: 2;
    display: grid;
    word-break: break-all;
    .c-cardArchive__title {
      order: 2;
      @include global.font_weight_bold;
      @include global.line_height(26, 14);
      @include global.md {
        @include global.line_height(26, 16);
      }
    }
    .c-cardArchive__meta {
      order: 1;
    }
    .c-cardArchive__time {
      color: #b7b7b7;
      letter-spacing: 0.02em;
      @include global.line_height(20, 14);
      @include global.font-EN(500);
      @include global.md {
        @include global.line_height(20, 16);
      }
    }
  }
  .c-cardArchive__thumbnail {
    order: 1;
    margin-bottom: global.vw_sp(13);
    overflow: hidden;
    @include global.md {
      margin-bottom: 10px;
    }
    .p-archives & {
      @include global.under_md {
        width: auto;
      }
    }
    img {
      width: 100%;
      height: 100%;
      @include global.object_fit(cover);
      transition: transform 0.5s ease;
    }
  }
}
