@charset "utf-8";

@use "../global";

.c-pageNav {
  .c-pageNav__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: global.vw_sp(5) global.vw_sp(5);
    @include global.md {
      grid-template-columns: repeat(4, 1fr);
      gap: global.per_w_pc(14);
    }
    li {
      a {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: global.vw_sp(50);
        padding-right: global.vw_sp(22);
        padding-left: global.vw_sp(22);
        background-color: #e2ebf5;
        border-radius: global.vw_sp(25);
        text-decoration: none;
        @include global.font_weight_bold;
        @include global.line_height(16, 14);
        @include global.fz(14);
        @include global.md {
          height: 50px;
          padding-right: 26px;
          padding-left: 22px;
          border-radius: 25px;
          @include global.line_height(22, 15);
          @include global.fz(15);
        }
        @include global.hover_opacity;
        &::after {
          display: block;
          flex-shrink: 0;
          width: global.vw_sp(10);
          height: global.vw_sp(6);
          margin-left: 10px;
          content: "";
          background: url("../img/angle_brackets_l.svg") no-repeat 0 0;
          background-size: global.vw_sp(10) auto;
          @include global.md {
            width: 10px;
            height: 6px;
            background-size: 10px auto;
          }
        }
      }
    }
  }
}
