@charset "utf-8";

@use "../global";

.l-contents {
  @include global.under_md {
    padding-right: global.vw_sp(10);
    padding-left: global.vw_sp(10);
  }
  .l-contents__wide {
    width: 100%;
  }
  &._narrow,
  .l-contents__narrow {
    @include global.md {
      max-width: global.$content_width_narrow;
      padding: 0 20px;
      margin: 0 auto;
    }
  }
}
