@charset "utf-8";

@use "../global";

.c-icon__arrow {
  display: inline-block;
  width: global.vw_sp(20);
  height: global.vw_sp(20);
  margin-right: global.vw_sp(8);
  background: #E2EBF5 url("../img/arrow.svg") no-repeat 50% 50%;
  background-size: global.vw_sp(10) auto;
  border-radius: 50%;
  @include global.md {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    background-size: 10px auto;
  }
  &._border {
    border: 1px solid global.$color_primary;
  }
}

.c-icon__home {
  display: inline-block;
  width: global.vw_sp(11);
  height: global.vw_sp(12);
  margin-right: global.vw_sp(3);
  background: url("../img/icon_home.svg") no-repeat 50% 50%;
  background-size: global.vw_sp(11) auto;
  @include global.md {
    width: 11px;
    height: 12px;
    margin-right: 3px;
    background-size: 11px auto;
  }
}

.c-icon__pdf {
  display: inline-block;
  width: 14px;
  height: 17px;
  margin-left: 6px;
  background: url("../img/icon_pdf_blue.svg") no-repeat 50% 50%;
  background-size: 14px auto;
}
