@charset "utf-8";

@use "../global";

.l-breadcrumb {
  @include global.under_md {
    padding: 0 global.vw_sp(10);
    margin-top: global.vw_sp(15);
    margin-bottom: global.vw_sp(40);
  }
  @include global.md {
    max-width: 1262px;
    padding: 0 20px;
    margin: 15px auto 60px;
  }
  .l-breadcrumb__list {
    display: flex;
    align-items: center;
    @include global.content_width(wide);
  }
  .l-breadcrumb__item {
    text-transform: uppercase;
    @include global.line_height(30, 12);
    @include global.fz(12);
    &:nth-child(n + 2) {
      display: flex;
      align-items: center;
      &::before {
        margin-right: 6px;
        margin-left: 6px;
        width: 4px;
        height: 7px;
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 6.108 10.803"><path fill="none" stroke="%23d8d8d8" d="M4946,1227.313l5.048,5.048-5.048,5.048" transform="translate(-4945.646 -1226.959)" stroke-width="1" /></svg>');
        background-size: 4px auto;
        content: "";
      }
    }
  }
  .l-breadcrumb__itemLink {
    color: global.$color_primary;
    @include global.hover {
      text-decoration: none;
    }
  }
}
