@charset "utf-8";

@use "../global";

.l-header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: global.$header_height_sp;
  background-color: global.$color_white;
  @include global.md {
    height: global.$header_height;
  }
  .l-header__inner {
    display: flex;
    align-items: center;
    width: 100%;
    @include global.under_tb {
      justify-content: space-between;
    }
    @include global.tb {
      padding: 0 20px 0 30px;
    }
  }
  .l-header__logo {
    position: relative;
    z-index: 10;
    @include global.under_tb {
      padding-left: global.vw_sp(10);
      &::after {
        position: absolute;
        top: global.vw_sp(-23);
        left: global.vw_sp(-10);
        z-index: 9;
        width: global.vw_sp(375);
        height: global.$header_height_sp;
        content: "";
        background: global.$color_white;
      }
    }
  }
  .l-header__logoLink {
    display: flex;
    @include global.hover_opacity;
    img {
      position: relative;
      z-index: 10;
      display: block;
      width: 223px;
      height: 38px;
      margin-top: auto;
      margin-bottom: auto;
      @include global.md {
        width: 278px;
        height: 69px;
      }
    }
  }
  .l-headerNav {
    margin-left: auto;
    @include global.under_tb {
      display: none;
    }
    .l-headerNav__list {
      display: flex;
      height: 100%;
      margin-top: 20px;
      @include global.under_ordinal_width(1310) {
        display: none;
      }
    }
    .l-headerNav__item {
      position: relative;
      height: 100%;
      & + .l-headerNav__item {
        border-left: 1px solid #d8d8d8;
      }
      .l-headerNav__itemWrap {
        height: 100%;
        cursor: pointer;
        &:not(:hover) {
          .c-itemsList {
            opacity: 0;
            visibility: hidden;
          }
        }
      }
      .l-headerNav__itemSubLink {
        &::after {
          position: absolute;
          left: 50%;
          bottom: 18px;
          width: 4px;
          height: 8px;
          content: "";
          background: url("../img/common/icon_anglebrackets.svg") no-repeat 0 0;
          background-size: cover;
          transform: rotate(90deg) translateX(-50%);
        }
      }
      .l-headerNav__itemSubLink,
      .l-headerNav__itemLink {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
        text-decoration: none;
        line-height: 1;
        @include global.font_weight_bold;
        @include global.fz(15);
        @include global.hover_opacity;
      }
      .c-itemsList {
        position: absolute;
        width: 323px;
        padding: 24px 32px 30px;
        margin-top: -10px;
        background: global.$color_white;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
        box-sizing: border-box;
        column-count: 2;
        .c-itemsList__item {
          margin-top: 3px;
          a {
            @include global.fz(13);
          }
        }
      }
    }
    .l-headerNav__info {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 42px;
      padding-right: 20px;
      @include global.under_tb {
        display: none;
      }
    }
    .l-headerNav__infoTel {
      padding-left: 25px;
      margin-right: 18px;
      background: url("../img/icon_tel.svg") no-repeat 0 53%;
      background-size: 21px auto;
      @include global.font-EN;
      @include global.fz(27);
    }
    .l-headerNav__infoBtn {
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 150px;
        height: 42px;
        color: global.$color_primary;
        background: #e2ebf5;
        border-radius: 25px;
        text-decoration: none;
        @include global.font_weight_bold;
        @include global.line_height(55, 15);
        @include global.fz(15);
        @include global.hover_opacity;
      }
    }
  }
  .l-header__globalNav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include global.under_md {
      padding-left: global.vw_sp(81);
      transition: background-color 0.3s linear;
      pointer-events: none;
    }
    @include global.md {
      opacity: 0;
      visibility: hidden;
      background: global.$color_white;
      transition: visibility 0.3s linear, opacity 0.3s linear;
    }
    &._open {
      @include global.under_md {
        background-color: rgba(global.$color_black, 0.16);
        transition: background-color 0.3s linear;
        pointer-events: auto;
      }
      @include global.md {
        visibility: visible;
        opacity: 1;
        transition: visibility 0.3s linear, opacity 0.3s linear;
      }
      .l-header__globalNavWrap {
        @include global.under_md {
          transform: translateX(0);
        }
      }
    }
    .l-header__globalNavWrap {
      padding-top: global.$header_height_sp;
      padding-bottom: 20px;
      @include global.under_md {
        height: 100vh;
        overflow: auto;
        background: global.$color_white;
        transform: translateX(100%);
        transition: transform 0.3s linear;
      }
      @include global.md {
        padding-top: 165px;
        padding-bottom: 100px;
      }
    }
    .l-header__globalNavInner {
      @include global.under_md {
        padding-right: global.vw_sp(25);
        padding-left: global.vw_sp(25);
      }
      @include global.md {
        display: flex;
        max-width: global.$content_width_narrow;
        margin: 0 auto;
      }
    }
    .c-items {
      @include global.under_md {
        margin-top: global.vw_sp(20);
      }
      .c-itemsList {
        .c-itemsList__item {
          margin-top: 3px;
          a {
            color: #535353;
            @include global.line_height(36, 16);
          }
        }
      }
    }
    .c-menu {
      @include global.under_md {
        margin-top: 28px;
      }
      @include global.md {
        display: grid;
        grid-template-columns: repeat(3, global.per_w_pc(244, 812));
        gap: 40px;
        width: 100%;
        max-width: 812px;
        margin: 0 auto;
      }
      .c-menuList {
        & + .c-menuList {
          @include global.under_md {
            margin-top: global.vw_sp(20);
          }
        }
      }
      .c-menuList__item {
        & + .c-menuList__item {
          margin-top: global.vw_sp(18);
          @include global.md {
            margin-top: 42px;
          }
        }
        > a {
          position: relative;
          display: block;
          padding-bottom: 18px;
          padding-left: 8px;
          color: #222222;
          text-decoration: none;
          border-bottom: 1px solid #d8d8d8;
          @include global.font_weight_bold;
          @include global.under_md {
            padding-bottom: 10px;
            letter-spacing: 0;
            @include global.line_height(30, 18);
            @include global.fz(18);
          }
          span {
            &::before {
              display: inline-block;
              width: 10px;
              height: 10px;
              margin-right: 11px;
              background: global.$color_primary;
              content: "";
            }
            &::after {
              position: absolute;
              top: 0.3em;
              right: 8px;
              display: inline-block;
              width: 7px;
              height: 11px;
              background: url("../img/angle_brackets_m.svg") no-repeat 0 0;
              background-size: 7px auto;
              content: "";
            }
            &._top {
              &::before {
                width: 18px;
                height: 20px;
                margin-right: 11px;
                margin-left: -5px;
                background: url("../img/icon_home.svg") no-repeat 0 0;
                background-size: 18px auto;
              }
            }
          }
        }
      }
    }
    .c-relatedMenu {
      @include global.under_md {
        margin-top: 32px;
      }
      @include global.md {
        margin-left: auto;
      }
    }
  }
}
