@charset "utf-8";

@use "../global";

.l-pagetop {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 68px;
  height: 68px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  @include global.under_md {
    width: 50px;
    height: 50px;
  }
  &._show {
    visibility: visible;
    opacity: 1;
    .l-pagetop__link {
      transform: translateY(0);
    }
  }
  .l-pagetop__link {
    position: absolute;
    display: block;
    width: 68px;
    height: 68px;
    background-color: rgba(#000, 0.15);
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
    transform: translateY(65px);
    @include global.under_md {
      width: 50px;
      height: 50px;
    }
    @include global.md {
      &:hover {
        opacity: 0.7;
      }
    }
    &::before {
      position: absolute;
      top: calc(50% - 30px);
      left: calc(50% - 11px);
      display: block;
      width: 22px;
      height: 37px;
      content: "";
      background: url(../img/icon_arrow_white.svg) no-repeat 0 0;
      background-size: 100% auto;
      transform: rotate(-180deg);
      transform-origin: center;
      @include global.under_md {
        top: calc(50% - 18px);
        left: calc(50% - 7px);
        width: 14px;
        height: 24px;
      }
    }
  }
}
