@charset "utf-8";

@use "../global";

input:-webkit-autofill {
  transition: background-color 5000s ease-in-out 0s !important;
}

.c-input {
  width: 100%;
  input[type="date"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="tel"],
  input[type="text"],
  input[type="time"] {
    box-sizing: border-box;
    padding: 10px 12px;
    background-color: global.$color_white;
    border: 1px solid #d5d5d5;
    border-radius: 6px;
    @include global.fz(14);
    @include global.md {
      padding: 15px;
    }
    &::placeholder {
      color: global.$color_gray;
    }
    //&:focus {
    //  outline: 2px solid global.$color_primary;
    //}
    &:focus-visible {
      outline: 2px solid global.$color_primary;
    }
  }
  input[name="Zip"] {
    margin-bottom: 13px;
    &::before {
      margin-right: 12px;
      content: "\03012";
    }
  }
  &._lage {
    input[type="date"],
    input[type="email"],
    input[type="number"],
    input[type="password"],
    input[type="tel"],
    input[type="text"],
    input[type="time"] {
      width: 100%;
    }
  }
  &._medium {
    input[type="date"],
    input[type="email"],
    input[type="number"],
    input[type="password"],
    input[type="tel"],
    input[type="text"],
    input[type="time"] {
      width: 50%;
      @include global.under_md {
        width: 100%;
      }
    }
  }
  &._small {
    input[type="date"],
    input[type="email"],
    input[type="number"],
    input[type="password"],
    input[type="tel"],
    input[type="text"],
    input[type="time"] {
      width: 140px;
      @include global.under_md {
        width: 100%;
      }
    }
  }
  input[type="checkbox"],
  input[type="radio"] {
    margin-right: 4px;
    @include global.md {
      margin-right: 8px;
    }
  }
}

span.c-input {
  & + span.c-input {
    margin-left: 20px;
    @include global.under_md {
      display: block;
      margin-left: 0;
    }
  }
}

p.c-input__help {
  margin-bottom: 10px;
}

span.c-input__help {
  margin-left: 10px;
  @include global.under_md {
    display: block;
    margin-top: 5px;
    margin-left: 0;
  }
}

.c-textarea {
  margin-top: 23px;
  &._narrow {
    margin: 28px auto 30px;
    @include global.lg {
      width: 76%;
    }
    textarea {
      @include global.fz(13);
      @include global.lg {
        height: 226px;
      }
    }
  }
  textarea {
    box-sizing: border-box;
    width: 100%;
    height: 150px;
    min-height: 80px;
    padding: 10px 12px;
    background-color: global.$color_white;
    border: 1px solid #d5d5d5;
    border-radius: 6px;
    @include global.md {
      height: 265px;
      padding: 20px;
    }
    &:focus-visible {
      outline: 2px solid global.$color_primary;
    }
  }
}
