@charset "utf-8";

@use "../global";

.c-hamburger {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 68px;
  height: 68px;
  border: none;
  border-radius: 50%;
  background: global.$color_primary;
  transition: background-color 0.3s global.$ease_out_quint;
  @include global.under_tb {
    margin-right: 10px;
  }
  @include global.tb {
    width: 86px;
    height: 86px;
  }
  @include global.hover_opacity;
  &::before {
    position: absolute;
    top: 33px;
    display: block;
    color: global.$color_white;
    content: "メニュー";
    @include global.font_weight_bold;
    @include global.fz(12);
    @include global.tb {
      top: 45px;
    }
    .l-header__globalNav._open + & {
      content: "閉じる";
    }
  }
  .c-hamburger__line {
    position: relative;
    display: block;
    width: 26px;
    height: 1px;
    margin-top: -18px;
    background-color: global.$color_white;
    transition: background-color 0.3s global.$ease_out_quint;
    &::before,
    &::after {
      position: absolute;
      left: 0;
      display: block;
      width: 26px;
      height: 1px;
      content: "";
      background-color: global.$color_white;
      transition: transform 0.3s global.$ease_out_quint;
    }
    &::before {
      top: -8px;
    }
    &::after {
      bottom: -8px;
    }
    .l-header__globalNav._open + & {
      background-color: initial;
      &::before {
        transform: translateY(8px) rotateZ(35deg);
      }
      &::after {
        transform: translateY(-8px) rotateZ(-35deg);
      }
    }
  }
}
