@charset "utf-8";

@use "../global";

.c-buttonWrap {
  &._left {
    display: flex;
    justify-content: flex-start;
  }
  &._center {
    display: flex;
    justify-content: center;
  }
  &._right {
    display: flex;
    justify-content: flex-end;
  }
}

.c-button {
  a {
    @include global.hover_opacity;
  }
  .c-button__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 65px;
    padding-right: 20px;
    padding-left: 20px;
    color: global.$color_white;
    text-decoration: none;
    text-transform: capitalize;
    background: linear-gradient(0.25turn, #218bcc 0%, #146ac1 100%);
    border-radius: 10px;
    @include global.font-JP_bold_sans;
    @include global.fz(16);
    @include global.lg {
      height: 68px;
      padding-right: 25px;
      padding-left: 25px;
    }
  }
  .c-button__blank {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 65px;
    padding-right: 20px;
    padding-left: 20px;
    color: global.$color_primary;
    text-decoration: none;
    text-transform: capitalize;
    background: global.$color_white;
    border: 2px solid global.$color_primary;
    border-radius: 10px;
    @include global.font-JP_bold_sans;
    @include global.fz(16);
    @include global.lg {
      height: 68px;
      padding-right: 25px;
      padding-left: 25px;
    }
    span {
      display: inline-block;
      padding-right: 18px;
      letter-spacing: 0.05em;
      background: url("../img/icon_window.svg") no-repeat 100% 50%;
      background-size: 14px auto;
      @include global.lg {
        padding-right: 23px;
        background-size: 13px auto;
      }
    }
    &._bule {
      color: global.$color_white;
      background: linear-gradient(0.75turn, #007db7 0%, #45a8d6 100%, #007db7 100%);
      border: none;
      span {
        background: url("../img/icon_window_white.svg") no-repeat 100% 50%;
        background-size: 14px auto;
      }
    }
  }
  .c-button__border {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 65px;
    padding-right: 20px;
    padding-left: 20px;
    color: global.$color_primary;
    text-decoration: none;
    text-transform: capitalize;
    border: 2px solid global.$color_primary;
    border-radius: 10px;
    @include global.font-JP_bold_sans;
    @include global.fz(16);
    @include global.lg {
      height: 68px;
      padding-right: 25px;
      padding-left: 25px;
    }
  }
  &._center {
    margin-right: auto;
    margin-left: auto;
  }
  & + & {
    @include global.under_lg {
      margin-top: 10px;
    }
    @include global.lg {
      margin-left: 20px;
    }
  }
  &._more {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: global.$color_white;
    background: linear-gradient(#155dd0 0%, #85d1e3 100%);
    @include global.font-EN(500);
    @include global.line_height(36, 14);
    @include global.fz(14);
    &._small {
      width: 107px;
      height: 35px;
      border-radius: 17.5px;
    }
    &._medium {
      width: 216px;
      height: 56px;
      border-radius: 30px;
    }
    @include global.hover_opacity;
  }
}

button.c-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 69px;
  padding-right: 20px;
  padding-left: 20px;
  color: global.$color_white;
  text-decoration: none;
  text-transform: capitalize;
  background: #0096d7;
  border: none;
  border-radius: 35px;
  @include global.font-JP_bold_sans;
  @include global.fz(16);
  @include global.lg {
    width: 380px;
    padding-right: 25px;
    padding-left: 25px;
    cursor: pointer;
    @include global.fz(18);
  }
  @include global.hover_opacity;
}


