@charset "utf-8";

@use "../global";

.p-contact {
  padding: 55px 18px 112px;
  background: global.$color_bg;
  @include global.lg {
    padding: 90px 0 117px;
  }
  .p-contact__heading {
    margin-bottom: 53px;
    color: global.$color_primary;
    text-align: center;
    letter-spacing: 0.05em;
    @include global.font-JP_bold_sans;
    @include global.fz(30);
    @include global.lg {
      margin-bottom: 56px;
      @include global.fz(34);
    }
  }
  .c-form {
    padding: 68px 20px 72px;
    background: global.$color_white;
    @include global.lg {
      max-width: 1200px;
      padding: 42px 6.7% 74px;
      margin: 0 auto;
      .c-buttonWrap .c-button {
        flex-basis: 379px;
        width: 379px;
      }
    }
  }
}
