@charset "utf-8";

@use "../global";

.c-form {
  .c-form__lead {
    letter-spacing: 0.05em;
    @include global.fz(14);
    @include global.line_height(24, 14);
  }
  .c-form__lead + dl {
    margin-top: 40px;
  }
  + .c-buttonWrap {
    margin-top: 46px;
  }
  .c-form__err {
    color: #d00404;
    @include global.fz(16);
    @include global.line_height(24, 16);
    + dl {
      margin-top: 20px;
    }
  }
  .c-form__item {
    + .c-form__item {
      margin-top: 30px;
    }
    .c-form__err {
      @include global.fz(14);
      @include global.line_height(24, 14);
    }
    dt {
      margin-bottom: 6px;
      color: global.$color_primary;
      letter-spacing: 0.05em;
      @include global.fz(14);
      @include global.line_height(24, 14);
      @include global.font-JP_bold_sans;
    }
    dd {
      label {
        display: block;
        letter-spacing: 0.05em;
        @include global.fz(14);
        @include global.line_height(24, 14);
        @include global.font-JP_bold_sans;
        + label {
          margin-top: 10px;
        }
      }
      > p {
        padding-top: 8px;
      }
    }
  }
  .c-buttonWrap {
    margin-top: 40px;
    @include global.lg {
      margin-top: 30px;
    }
  }
}

.c-form__required {
  color: #e80808;
  @include global.fz(14);
}
